var mods = require('./mods')
var axios = require("./lib/axios")
var MyPromise = require("./MyPromise")
var wxPay = require('./wxPay')

axios.defaults.timeout = 30000;
axios.defaults.headers = {
    "Content-Type": "application/json",
    "Accept": "application/json"
};

module.exports = {
    /**
     *  发起支付并监听支付返回结果
     * @param charge  支付数据
     * @param timeout 结果监听超时时间，默认 180s
     * @param payCallback 支付结果回调 返回结果对象
     */
    doPay: function (charge, timeout, payCallback) {
        let result = {}
        // 校验charge 必填：pay_channel，amount 非必填：subject，body
        let check = this.checkCharge(charge)
        if (check) {
            mods.payResult.paramError.result_message = check
            payCallback(mods.payResult.paramError)
            return
        }

        // 二维码主扫
        if (charge.pay_channel === 'alipay_qr' || charge.pay_channel === 'wx_qr') {
            this.handlePayResult(charge.query_url, timeout).then(function (res) {
                payCallback(res)
            })
        }else if (charge.pay_channel === 'alipay_wap'){ // 支付宝H5
            this.aliPay(charge, timeout, function (pay_result) {
                payCallback(pay_result)
            })
        }else if (charge.pay_channel === "wx_pub"){ // 微信H5
            this.wxPay(charge, timeout, function (pay_result) {
                payCallback(pay_result)
            })
        }else{
            mods.payResult.paramError.result_message = mods.chackMsg.channelError

            payCallback(mods.payResult.paramError)
        }
    },

    /**
     *  支付宝操作
     * @param charge 支付信息
     * @param timeout 支付查询结果
     * @param aliPayCallback 支付宝支付结果回调
     */
    aliPay: function (charge, timeout, aliPayCallback) {
        // 如果是 livemode 模式打开的url则是用测试页拼接的连接 否则是正常连接
        let url = charge.livemode === "false" ? charge.extra.pay_info + "?charge_id=" + charge.id + "&pay_channel=" + charge.pay_channel : charge.extra.pay_info

        this.os.isPc ? window.open(url) : window.location.href = url

        this.handlePayResult(charge.query_url, timeout).then(function (res) {
            aliPayCallback(res)
        })
    },

    /**
     *  微信支付操作
     * @param charge 支付信息
     * @param timeout 支付查询结果
     * @param wxPayCallback 微信支付结果回调
     */
    wxPay: function (charge, timeout, wxPayCallback) {
        if (charge.livemode === "false") {
            let url = charge.extra.pay_info + "?charge_id=" + charge.id + "&pay_channel=" + charge.pay_channel

            this.os.isPc ? window.open(url) : window.location.href = url

            this.handlePayResult(charge.query_url, timeout).then(function (res) {
                wxPayCallback(res)
            })
        }else{
            let self = this
            // 微信 sdk 
            wxPay.wxPay(charge, function (status) {
                if (status === mods.payStatus.cancel) {
                    wxPayCallback(mods.payResult.cancel)
                }else if (status === mods.payStatus.failed) {
                    wxPayCallback(mods.payResult.failed)
                }else{
                    self.handlePayResult(charge.query_url, timeout).then(function (res) {
                        wxPayCallback(res)
                    })
                }
            })
        }
    },

    /**
     *  轮询支付结果 返回 Promise
     * @param query_url 轮询 url
     * @param timeout 轮询超时时间
     */
    handlePayResult: function (query_url, timeout) {
        var idx = 0
        let self = this
        // 定时器id
        var intervalID = undefined
        let result_info = {}

        return new MyPromise(function (resolve, reject) {
            intervalID = setInterval(function () {
                // 如果轮询时间结束 取消定时器并返回支付超时状态
                if (idx > timeout) {
                    clearInterval(intervalID)

                    mods.payResult.timeout.result_info = result_info
                    resolve(mods.payResult.timeout)
                }
                idx = idx + 3;
                // 请求轮询接口
                self.requestPayResult(query_url).then(function (res) {
                    result_info = res.result_info

                    // 如果是支付成功或失败都结束查询 否则继续轮询
                    if (res.result_status === mods.payStatus.succeeded || res.result_status === mods.payStatus.failed) { 
                        clearInterval(intervalID)
                        resolve(res)
                    }
                })
            }, 3000)
        })
    },

    /**
     *  轮询支付结果的网络请求
     */
    requestPayResult: function (query_url) {
        return new MyPromise(function (resolve, reject) {
            axios.get(query_url).then(function (response) {
                let data = response.data
                // 支付成功
                if (data.status === mods.payStatus.succeeded) { 
                    
                    mods.payResult.succeeded.result_info = data
                    resolve(mods.payResult.succeeded)
                }else if (data.status === mods.payStatus.pending) { // 支付中
                    mods.payResult.pending.result_info = data
                    resolve(mods.payResult.pending)
                }else{
                    // 由于支付失败或者支付中等状态都是由 restful 风格，包在error中顾直接返回 unknown
                    mods.payResult.unknown.result_info = data
                    resolve(mods.payResult.unknown)
                }
            }).catch(function (error) {
                // error.response 中包含除支付成功的信息
                if (error && error.response) {
                    let data = error.response.data

                    mods.payResult.failed.result_info = data
                    resolve(mods.payResult.failed)
                }else{ // unknown 其他状态
                    mods.payResult.unknown.result_info = {}
                    resolve(mods.payResult.unknown)
                }
            });
        })
    },

    /**
     *  支付数据的校验
     */
    checkCharge: function (params) {
        let channelArr = mods.channelConfig
        
        if (params.status !== "succeeded") {
            return mods.chackMsg.orderError
        }
        // 验证传入的支付渠道是否正确
        if (channelArr.indexOf(params.pay_channel) < 0) { 
            return mods.chackMsg.channelError
        }
        
        return undefined
    },

    /**
     *  检验当前浏览器内核
     */
    os: function() {
        var ua = navigator.userAgent,
        isWindowsPhone = /(?:Windows Phone)/.test(ua),
        isSymbian = /(?:SymbianOS)/.test(ua) || isWindowsPhone, 
        isAndroid = /(?:Android)/.test(ua), 
        isFireFox = /(?:Firefox)/.test(ua), 
        isChrome = /(?:Chrome|CriOS)/.test(ua),
        isTablet = /(?:iPad|PlayBook)/.test(ua) || (isAndroid && !/(?:Mobile)/.test(ua)) || (isFireFox && /(?:Tablet)/.test(ua)),
        isPhone = /(?:iPhone)/.test(ua) && !isTablet,
        isPc = !isPhone && !isAndroid && !isSymbian;
        return {
            isTablet: isTablet,
            isPhone: isPhone,
            isAndroid : isAndroid,
            isPc : isPc
        };
    }()
}


